import React, {useEffect, useState} from 'react';
import './style.scss';
import {getGains} from "../../service";
import {connect, Loading} from "../../components";

function Records({address}) {
    const [list, setList] = useState([]);
    const [loading, setLoading] = useState(false);

    useEffect(() => {
        if (!address) return;
        setLoading(true);
        getGains({address}).then(({data}) => {
            if (data.ret) {
                setList(data.data.list);
            }
            setLoading(false);
        }, () => {
            setLoading(false);
        });

    }, [address]);

    function formatTime (time) {
        const t = new Date(time*1000);
        const month = t.getMonth() + 1;
        const date = t.getDate();
        const hours = t.getHours();
        const minutes = t.getMinutes();

        return `${month}月${date}日 ${hours}:${minutes}`;

    }

    return (
        <div className='p-records'>
            {loading && <Loading /> }
            {list.map(({type, createtime, amount}, index) => {
                let name;
                switch (type) {
                    case 0:
                        name = '民主证书';
                        break;
                    case 1:
                        name = '创世兑换';
                        break;
                    default:
                        name = `type ${type}`;
                }
                return (
                    <div key={index} className='list-item'>
                        <div>
                            <p>{name}</p>
                            <p>{formatTime(createtime)}</p>
                        </div>
                        <div>+{amount/1e8}BTC</div>
                    </div>
                )
            })}
        </div>
    )
};

export default connect()(Records);
